<!--我发布的车单
 * @Description: 审核订单-车
 * @Version: Do not edit
 * @Autor: gitxinghui
 * @Date: 2020-12-03 22:51:47
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2021-01-10 19:08:14
-->
<template>
    <view class="demand_car">
        <!-- tabbar -->
        <cu-custom><block slot="content">我发布的车单</block></cu-custom>
        <view class="demand_car_list">
            <!-- 导航栏 -->
            <scroll-view scroll-x class="bg-white nav">
                <view class="flex text-center navBox">
                    <view class="cu-item flex-sub" :class="item.id==tabCur?'things_active_color':''" v-for="(item,index) in tabData" :key="index" @tap="tabSelect" :data-id="item.id">
                        {{item.name}}
                    </view>
                </view>
            </scroll-view>
            <!-- 审核中-列表 -->
            <scroll-view v-if="tabCur == 1" class="car_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="car_item" v-for="(item,index) in listData[0].list" :key="index"  :data-id="item.orderId" @tap="linkToOrderInfo">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 订单信息 -->
                    <view class="order_info">
                        <view class="car_single_info">订单编号：{{item.orderNo}}</view>
                        <view class="things_width_full flex justify-start align-center car_double_info">
                            <view class="car_left_info">车牌号：{{item.carNumber}}</view>
                            <view class="car_right_info">挂车牌号：{{item.listingCarNumber}}</view>
                        </view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" :data-index="index" :data-id="item.orderId" @tap.stop="cancelOrderFun">取消订单</button>
                    </view>
                </view>
                <view v-if="listData[0].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 未通过-列表 -->
            <scroll-view v-if="tabCur == 2" class="car_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="car_item" v-for="(item,index) in listData[1].list" :key="index"  :data-id="item.orderId" @tap="linkToOrderInfo">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 订单信息 -->
                    <view class="order_info">
                        <view class="car_single_info">订单编号：{{item.orderNo}}</view>
                        <view class="things_width_full flex justify-start align-center car_double_info">
                            <view class="car_left_info">车牌号：{{item.carNumber}}</view>
                            <view class="car_right_info">挂车牌号：{{item.listingCarNumber}}</view>
                        </view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-between align-center operating">
                        <view class="time">发布时间：{{item.createTime}}</view>
                        <button class="cu-btn bg-white grabOrderBtn" :disabled="buttonType" :data-id="item.orderId" @tap.stop="linkToUpdate">修改发布</button>
                    </view>
                    <!-- 拒绝原因 -->
                    <view class="reason">原因：{{item.release_examine_remark?item.release_examine_remark:'暂无'}}</view>
                </view>
                <view v-if="listData[1].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 待确认-列表 -->
            <scroll-view v-if="tabCur == 3" class="car_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="car_item" v-for="(item,index) in listData[2].list" :key="index" :data-id="item.orderId" @tap="linkToOrderInfo">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 订单信息 -->
                    <view class="order_info">
                        <view class="car_single_info">订单编号：{{item.orderNo}}</view>
                        <view class="things_width_full flex justify-start align-center car_double_info">
                            <view class="car_left_info">车牌号：{{item.carNumber}}</view>
                            <view class="car_right_info">挂车牌号：{{item.listingCarNumber}}</view>
                        </view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" :data-index="index" :data-id="item.orderId" @tap.stop="cancelOrderFun">取消订单</button>
                        <button class="cu-btn bg-white detail_btn" :disabled="buttonType" :data-id="item.orderId" @tap.stop="linkToOrderInfo">订单详情</button>
                    </view>
                </view>
                <view v-if="listData[2].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 交易中-列表 -->
            <scroll-view v-if="tabCur == 4" class="car_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="car_item" v-for="(item,index) in listData[3].list" :key="index"  :data-id="item.orderId" @tap="linkToOrderInfo">
                  <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 订单信息 -->
                    <view class="order_info">
                        <view class="car_single_info">订单编号：{{item.orderNo}}</view>
                        <view class="things_width_full flex justify-start align-center car_double_info">
                            <view class="car_left_info">车牌号：{{item.carNumber}}</view>
                            <view class="car_right_info">挂车牌号：{{item.listingCarNumber}}</view>
                        </view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" :data-index="index" :data-id="item.orderId" @tap.stop="cancelOrderFun" v-if="item.status <= 4">取消订单</button>
                        <button class="cu-btn bg-white confirm_transit_btn" :disabled="buttonType" :data-id="item.orderId" @tap.stop="confirmTransportFun" v-if="item.status === 6">确认运输</button>
                        <button class="cu-btn bg-white complete_transit_btn" :disabled="buttonType" :data-id="item.orderId" @tap.stop="linkToUploadReceipt" v-if="item.status === 8">完成运输</button>
                    </view>
                </view>
                <view v-if="listData[3].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
            <!-- 已完成-列表 -->
            <scroll-view v-if="tabCur == 5" class="car_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="car_item" v-for="(item,index) in listData[4].list" :key="index"  :data-id="item.orderId" @tap="linkToOrderInfo">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 订单信息 -->
                    <view class="order_info">
                        <view class="car_single_info">订单编号：{{item.orderNo}}</view>
                        <view class="things_width_full flex justify-start align-center car_double_info">
                            <view class="car_left_info">车牌号：{{item.carNumber}}</view>
                            <view class="car_right_info">挂车牌号：{{item.listingCarNumber}}</view>
                        </view>
                    </view>
                    <!-- 操作区域 -->
                    <!-- <view class="flex justify-end align-center operating">
                        <button class="cu-btn bg-white cancel_btn">评价</button>
                    </view> -->
                </view>
                <view v-if="listData[4].list.length <= 0" class="things_width_full kong_data">
                    <image class="kong_image" src="./../../../../../../static/images/global/kong.png" mode="widthFix" />
                    <view class="kong_tip">暂无数据</view>
                </view>
            </scroll-view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
import { userReleaseTruckList_api,demandCancelOrders_api,confirmTransactionReleaseCars_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            // 全局按钮状态
            buttonType: false,
            appBaseData:null,//全局配置信息
            userInfo: {},
            tabCur: 1,
            scrollLeft: 0,
            tabData: [
                {
                    id: 1,
                    value: 10,
                    name: '审核中',
                },
                {
                    id: 2,
                    value: 20,
                    name: '未通过'
                },
                {
                    id: 3,
                    value: 30,
                    name: '待确认'
                },
                {
                    id: 4,
                    value: 40,
                    name: '交易中'
                },
                {
                    id: 5,
                    value: 50,
                    name: '已完成'
                }
            ],
            listData: [
                // 审核中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 未通过
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 待确认
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 运输中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 已完成
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                }
            ],
            // 全局按钮状态
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.base_refreshPage': function(val) {
            if (val) {
                // 更新数据
                that.clearPageFun();
                that.base({name:'refreshPage',value:false});
            }
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    computed: {
        ...mapGetters(['base_cancelOrderType']) 
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.listData = [
                // 审核中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 未通过
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 待确认
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 运输中
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                },
                // 已完成
                {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                }
            ];
            that.getCarOrder();
        },
        /**导航选择
         * @description: 
         * @param {*} e
         * @return {*}
         */
        tabSelect(e) {
            if(that.tabCur == e.currentTarget.dataset.id) return false;
            that.tabCur = e.currentTarget.dataset.id;
            that.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
            // 判断当前列表有没有数据,没有就qui请求数据
            if (that.listData[that.tabCur - 1].list.length <= 0 && that.listData[that.tabCur - 1].pageMore) {
                that.getCarOrder();
            }
        },
        /**取消订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        cancelOrderFun(e) {
            let params = {
                order_id: e.currentTarget.dataset.id,
                type: that.base_cancelOrderType[1].value,
            };
            that.buttonType = true;
            demandCancelOrders_api(params,that).then(res=>{
                that.$delete(that.listData[that.tabCur-1].list, e.currentTarget.dataset.index);
                that.buttonType = false;
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'取消成功'}});
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /**跳转更新订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToUpdate(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/demand/car/updateOrder/index?truck_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**跳转上传收据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToUploadReceipt(e) {
            uni.navigateTo({
                url: `/pages/things/user/demand/car/uploadReceipt/index?order_id=${e.currentTarget.dataset.id}`
            });
        },
        /**点击开始运输
         * @description: 
         * @param {*} e
         * @return {*}
         */
        confirmTransportFun(e) {
            let params = {
                release_order_id: e.currentTarget.dataset.id,
            };
            that.buttonType = true;
            confirmTransactionReleaseCars_api(params,that).then(res=>{
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'请开始运输货物'}});
                that.listData[3] = {
                    page: 1,
                    page_size: 10,
                    pageMore: true,
                    list: [],
                };
                that.buttonType = false;
                that.getCarOrder();
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /**跳转到需求详情
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToOrderInfo(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/demand/car/info/index?order_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**获取发布的车单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getCarOrder(e) {
            let params = {
                page: that.listData[that.tabCur-1].page,
                page_size: that.listData[that.tabCur-1].page_size,
                status: that.tabData[that.tabCur-1].value
            };
            userReleaseTruckList_api(params,that).then(res=>{
                that.updateListData(res.list,res.total);
            }).catch(err=>{
                console.log(err);
            })
        },
        /**更新数据
         * @description: 
         * @param {*} data 数组列表
         * @param {*} total 列表总长度
         * @return {*}
         */
        updateListData(data=[],total=0) {
            switch (Number(that.tabData[that.tabCur-1].value)) {
                case 10:
                    that.listData[0].list = that.listData[0].list.concat(data);
                    if (that.listData[0].list.length >= total) {
                        that.listData[0].pageMore = false;
                    }else{
                        that.listData[0].page++;
                    }
                    break;
                case 20:
                    that.listData[1].list = that.listData[1].list.concat(data);
                    if (that.listData[1].list.length >= total) {
                        that.listData[1].pageMore = false;
                    }else{
                        that.listData[1].page++;
                    }
                    break;
                case 30:
                    that.listData[2].list = that.listData[2].list.concat(data);
                    if (that.listData[2].list.length >= total) {
                        that.listData[2].pageMore = false;
                    }else{
                        that.listData[2].page++;
                    }
                    break;
                case 40:
                    that.listData[3].list = that.listData[3].list.concat(data);
                    if (that.listData[3].list.length >= total) {
                        that.listData[3].pageMore = false;
                    }else{
                        that.listData[3].page++;
                    }
                    break;
                default:
                    break;
            }
        },
        /**上拉加载
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getMoreData(e) {
            let getType = false;
            switch (Number(that.tabData.value)) {
                case 10:
                    getType = that.listData[0].pageMore;
                    break;
                case 20:
                    getType = that.listData[1].pageMore;
                    break;
                case 30:
                    getType = that.listData[2].pageMore;
                    break;
                case 40:
                    getType = that.listData[3].pageMore;
                    break;
                default:
                    break;
            }
            if (!getType) return false;
            that.getCarOrder();
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        that.tabCur = options.type?options.type:1;
        that.getCarOrder();
    }
}
</script>

<style lang="scss" scoped>
    @import "./index";
</style>